<template>
  <div class="batchDeleteModal">
    <a-modal class="deleteModal" title="" :maskClosable="maskClosable" :visible="visible" @ok="handleOk" :confirmLoading="confirmLoading" @cancel="handleCancel" width="530px">
      <a-form :form="form">
        <a-alert type="info" :show-icon="true">
          <div slot="message">
            &nbsp;<a style="font-weight: 600">{{list_Num}}</a>&nbsp;&nbsp;&nbsp;
          </div>
        </a-alert>
        <a-form-item label="" :labelCol="{ span: 7 }" :wrapperCol="{ span: 12 }">
          <a-select v-decorator="['operation',{initialValue:'select'}]" :size="size" style="width:100px" @change="handleChange">
            <a-select-option value="select">
              
            </a-select-option>
            <a-select-option value="search">
              
            </a-select-option>
            <a-select-option  value="all">
                
            </a-select-option>
          </a-select>
        </a-form-item>
        <!-- <a-form-item label="" :labelCol="{ span: 7 }" :wrapperCol="{ span: 12 }"> -->
        <div class="flex">
          <a-form-item label="" :labelCol="{ span: 7 }" :wrapperCol="{ span: 12 }">
            <span style="margin-right:5px"></span>
            <a-input-number v-decorator="['limit',{initialValue:0}]" :size="size" :min="0" :max="limitMax" style="width: 60px" :disabled="disabledPart" @change="inputNumChange" />
            <span></span>
            <a-divider type="vertical" />
          </a-form-item>
          <a-form-item>
            <a-checkbox v-decorator="['all_record',{initialValue:true}]" defaultChecked @change="onAllCheckBoxChange"></a-checkbox>
          </a-form-item>
        </div>
        <!-- </a-form-item> -->
        <!-- <a-form-item label="" :labelCol="{ span: 7 }" :wrapperCol="{ span: 12 }">
          <a-checkbox v-decorator="['associateby_record',{initialValue:false}]" @change="onAssoRecordChange" />
        </a-form-item> -->
      </a-form>
    </a-modal>
  </div>
</template>

<script>
  import AModal from 'ant-design-vue/lib/modal'
  import AForm from 'ant-design-vue/lib/form'
  import AButton from 'ant-design-vue/lib/button'
  import ASelect from 'ant-design-vue/lib/select'
  import AInputNumber from 'ant-design-vue/lib/input-number'
  // import 'ant-design-vue/lib/input-number/style/index.less'
  import ACheckbox from 'ant-design-vue/lib/checkbox'
  import AAlert from 'ant-design-vue/lib/alert'
  import ADivider from 'ant-design-vue/lib/divider'
  // import { listeners } from 'cluster';
  const AFormItem = AForm.Item
  const ASelectOption = ASelect.Option
  export default {
    props: {
      selectedRowId: Array,
      customerNum: Number
    },
    components: {
      AModal,
      AForm,
      AFormItem,
      AButton,
      ASelect,
      ASelectOption,
      AInputNumber,
      ACheckbox,
      AAlert,
      ADivider
    },
    data() {
      return {
        maskClosable:false,//modal
        size: "small",
        visible: false,
        confirmLoading: false,
        disabledPart: true,
        currentOpera: 'select',
        currentLimit: 0,
        currentRecord: true,
        limitMax: 0,
      }
    },
    beforeCreate() {
      this.form = this.$form.createForm(this)
    },
    computed: {
      list_Num() {
        if (this.currentOpera === "select") {
          this.limitMax = this.$props.selectedRowId.length
          return this.currentRecord ? this.limitMax : this.currentLimit
        } else if (this.currentOpera === "search") {
          this.limitMax = this.$props.customerNum
          return this.currentRecord ? this.limitMax : this.currentLimit
        } else {
          this.limitMax = 0
          return 0
        }
      }
    },
    methods: {
      showModal() {
        this.visible = true
      },
      handleOk(e) {
        e.preventDefault();
        let _this = this
        this.confirmLoading = true;
        this.form.validateFields((err, values) => {
          if (!err) {
              if (values.operation === "select") {
                values.id_list = (_this.$props.selectedRowId).join(",")
              } else if (values.operation === "search") {
                values.where = _this.$store.state.accounts.condition.where
              } else if (values.operation === "all") {
  
              } else {
                _this.confirmLoading = false;
                _this.$notification['warning']({
                  message: '',
                })
                return
              }
              values.all_record = values.all_record?'yes':'no'
            _this.sendDeleteDataToServer(values)
          } else {
            _this.$notification['warning']({
                  message: '',
                })
          }
        })
      },
      handleCancel() {
        this.visible = false
      },
      handleChange(value) {
        this.currentOpera = value
      },
      inputNumChange(value) {
        this.currentLimit = value
      },
      onAllCheckBoxChange(e) {
        const checked = e.target.checked
        if (e.target.checked) {
          this.currentRecord = checked
          this.form.setFieldsValue({
            limit: 0
          }) //
          this.currentLimit = 0
        } else {
          this.currentRecord = checked
        }
        this.disabledPart = !this.disabledPart;
      },
      // onAssoRecordChange(e) {
      // },
      sendDeleteDataToServer(params) {
        let _this = this
        this.$api.batchDelete(params).then((result) => {
          _this.confirmLoading = false
          _this.visible = false
          _this.$emit('refreshCustInfo')
        }).catch((err) => {
          _this.confirmLoading = false
          _this.$myNotification.sendFailedNotification(err)
        });
      }
    }
  }
</script>

<style lang="less" scoped>
  .ant-input-number {
    margin-right: 5px;
    border-radius: 4px
  }
  
  div.deleteModal .ant-row.ant-form-item {
    margin-bottom: 0
  }
</style>

<style>
  div.deleteModal div.ant-form-item-label {
    width: 140px;
  }
  
  div.deleteModal div.ant-form-item-control {
    text-align: start
  }
</style>




